/* registration page */
html,body{
  overflow-x: hidden;
  overflow-y: hidden;
}
.signup-form {
  width: "480px";
  height: "100px";
  padding: "30px";
  margin-left: "start";
}

.body {
  background: #ffffff;
  font-family: "Roboto", sans-serif;
}

.form-control {
  font-size: 16px;
  transition: all 0.4s;
  box-shadow: none;
}
.form-control:focus {
  border-color: #5cb85c;
}
.form-control,
.btn {
  border-radius: 50px;
  outline: none;
}
.signup-form {
  width: 400px;
  height: 10vh;
  margin: 10px;
  padding: 8px 8px;
}
.signup-form form {
  border-radius: "5px";
  margin-bottom: "20px";
  background: "#fff";
  box-shadow: 2px 2px 10px 2px rgba(0, 0, 0, 0.3);
  color: white;
  padding: 40px;
  border: 2px solid grey;
  border-radius: 24px;
}

.signup-form a {
  width: 100%;
  color: #ed3095;
}
.signup-form h2 {
  text-align: center;
  font-size: 34px;
  margin: 10px 0 15px;
}
.signup-form .hint-text {
  color: #999;
  text-align: center;
  margin-bottom: 20px;
}
.signup-form .form-group {
  margin-bottom: 20px;
}
.signup-form .btn {
  font-size: 18px;
  line-height: 26px;
  font-weight: bold;
  text-align: center;
}
.signup-btn {
  text-align: center;
  border-color: #5cb85c;
  transition: all 0.4s;
}
.signup-btn:hover {
  background: #ed3095;
  opacity: 0.8;
}
.or-seperator {
  margin: 50px 0 15px;
  text-align: center;
  border-top: 1px solid #e0e0e0;
}
.or-seperator b {
  padding: 0 10px;
  width: 40px;
  height: 40px;
  font-size: 16px;
  text-align: center;
  color: black;
  line-height: 40px;
  background: #fff;
  display: inline-block;
  border: 1px solid #e0e0e0;
  border-radius: 50%;
  position: relative;
  top: -22px;
  z-index: 1;
}
.social-btn .btn {
  color: #fff;
  margin: 10px 0 0 15px;
  font-size: 15px;
  height: 40px;
  border-radius: 50px;
  font-weight: normal;
  border: none;
  transition: all 0.4s;
}
.social-btn .btn:first-child {
  margin-left: 0;
}
.social-btn .btn:hover {
  opacity: 0.8;
}
.social-btn .btn-primary {
  background: #507cc0;
}
.social-btn .btn-info {
  background: #64ccf1;
}
.social-btn .btn-danger {
  background: #df4930;
}
.social-btn .btn i {
  float: left;
  margin: 3px 10px;
  font-size: 20px;
}
.text-center {
  color: white;
  margin-right: 5px;
}

